import { createStore } from 'vuex'
import DateStore from './date'
export default createStore({
    modules: {
        DateStore
    },
    state() {
        return {
            entered: false,
            lastName: ' ',
            firstName: ' ',
        }
    },
    getters: {
        isEntered(state) {
            return state.entered
        },
        getFullName(state) {
            return state.lastName + ' ' + state.firstName
        },
    },
    mutations: {
        newLastName(state, str) {
            state.lastName = str
        },
        newFirstName(state, str) {
            state.firstName = str
        },
        newEntered(state, bol) {
            state.entered = bol
        },
    },
    actions: {
        complited({ commit, state }, obj) {
            commit('newLastName', obj.LastName)
            commit('newFirstName', obj.firstName)
            commit('newEntered', true)
            localStorage.setItem('name', JSON.stringify({ lastName: state.lastName, firstName: state.firstName, entered: true }))
        },
        fullName({ commit }) {
            let name = JSON.parse(localStorage.getItem('name'))
            if (name != null) {
                commit('newLastName', name.lastName)
                commit('newFirstName', name.firstName)
                commit('newEntered', name.entered)
            }
        },
        outProfile({ commit }) {
            commit('newEntered', false)
            localStorage.removeItem('name')
        },
    },
    strict: process.env.NODE_ENV !== 'production',
});