export default {
    namespaced: true,
    state() {
        return {
            date: 19,
        }
    },
    getters: {
        getDate(state) {
            return state.date
        }
    },
    mutations: {
        newDate(state, newValue) {
            console.log(newValue);
            state.date = newValue
        },
    },
    actions: {},
}