<template>
     <div class="filter__date">
        <h2 class="filter__date-title">filter date dropdown</h2>
        <select @change="dateValueComponent" v-model="dateValue" class="filter__date-select" name="filter" id="filter">
            <option value="19">19 авг - 23 авг</option>
            <option value="23">23 авг - 26 авг</option>
            <option value="26">26 авг - 29 авг</option>
        </select>
    </div>
</template>

<script>
import { mapMutations } from 'vuex'

export default{
    data(){
        return{
            dateValue:'19',
        }
    },
    methods:{
        ...mapMutations('DateStore',{newDate:'newDate'}),
        dateValueComponent(){
            this.newDate(this.dateValue)
        }
    },
}
</script>