<script>
import { mapGetters } from 'vuex'
import { mapActions } from 'vuex'
export default{
    data(){
        return{
            nav:true
        }
    },
    computed:{
        ...mapGetters(['isEntered','getFullName']),
        navWatch(){
          return this.nav ? '1' : '0' 
        },
    },
    methods:{
      ...mapActions(['outProfile']),
        RigisterOpen(){
            this.$emit('RigisterOpen',)
