        ]
      }
  },
  methods:{
      nextSlide(){
          this.order = this.order + 1
          if(this.order == (this.sliderItems.length)){
            this.order = 0
          }
      },
      prefSlide(){
          this.order = this.order - 1
          if(this.order < 0){
          this.order = this.sliderItems.length-1
          }
      },
      anySlide(indx){
           this.order = indx
      }
  },
  computed:{
      slideTransform(){
            return  `transform: translateX(-${(+this.wraperWidth * this.order)}px); 
