<script>
import DateVue from '@/components/Date-vue'
import FilterDateVue from '@/components/FilterDate-vue'
import SliderVue from '@/components/Slider-vue'
    export default{
      components: {
    DateVue,
    FilterDateVue,
    SliderVue
  },
        data(){
            return{
                activClass:{
                    dropdown__active:false,
                },
                quests: [{title:'Взрослые',size:0},{title:'Дети',size:0},{title:'Младенцы',size:0}],
                dropdownText:'Сколко гостей ?'
            }
        },
        computed:{
          sizeQuests(){
            return this.quests.reduce((acc,Value ) => Value.size + acc ,0)
          },

        },
        methods:{
          decriment(indx){
            this.quests[indx].size != 0 ? this.quests[indx].size -=1 : this.quests[indx].size
          },
          incriment(indx){
             this.quests[indx].size += 1
          },
          mainextQuests(){
            let result = this.sizeQuests
            this.dropdownText =  result == 0 ? ' Сколько гостей' : result +' Гостя(-ей)'
          },
          crearQuests(){
            this.quests.map(el => el.size = 0)
            this.mainextQuests()
          },
        }
    }
</script>



