<template>
     <div class="modal modal-entries">
                <div class="modal-entries__inner">
                    <h2 class="modal__title entries__title">Войти</h2>
                    <div class="modal-date">
                        <input v-model="value.Email" class="margin-modal" type="email" placeholder="Email">
                        <input v-model="value.Password" class="margin-modal" type="password" placeholder="Пароль">
                    </div>
                    <div class="modal-button">
                        <button @click="enter" class="button button__modal">Перейти к оплате<span class="modal-arrow">&#10132;</span></button>
                    </div>
                    <div class="modal-footer">
                        <p class="footer-title">Нет аккаунта на Toxin?</p>
                        <button  @click="openRegister" class="button button__enter">Создать</button>
                    </div>
                </div>
            </div>
</template>


<script >
import { mapActions } from 'vuex'
export default{

    data(){
        return{
            value:{
                Email:'',
                Password:'',
            },
        }
    },
    methods:{
        ...mapActions(['complited']),
        enter(){
           let user = JSON.parse(localStorage.getItem('User'))
           if(user.Email == this.value.Email && user.Password == this.value.Password){
               this.complited(user)
               this.$emit('closeEnter')
           }
        },
        openRegister(){
            this.$emit('openRegister')
        },
    },

}
</script>



<style >
.modal-entries__inner {
  width: 350px;
  height: 340px;
  -webkit-box-shadow: 1px 0px 13px 0px rgba(50, 50, 50, 0.75);
          box-shadow: 1px 0px 13px 0px rgba(50, 50, 50, 0.75);
  padding: 30px 40px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

.entries__title {
  margin-bottom: 20px;
}

</style>