<template>
    <div class="modal modal-register">
                <div class="modal-inner">
                    <h2 class="modal__title">Регистрация аккаунта</h2>
                    <input 
                    v-model="registerDate.firstName" 
                    class="modal__first-name" 
                    type="text" 
                    placeholder="Имя"
                    @change="validateSize($event,3)"
                    >
                    <input 
                    v-model="registerDate.LastName" 
                    class="modal__last-name" 
                    type="text" 
                    placeholder="Фамилия"
                    @change="validateSize($event,4)"
                    >

                    <div class="modal-floor">
                        <div @click="male = true;registerDate.floor =floorMan" class="modal-floor-item">
                            <div class="circle">
                                <div :class="male ? 'circle-active' : ''" class="circle-item"></div>
                            </div>
                            <p class="modal-floor-item-text">Мужчина</p>
                        </div>
                        <div @click="male = false;registerDate.floor =floorMan" class="modal-floor-item">
                            <div class="circle">
                                <div :class="!male ? 'circle-active' : ''" class="circle-item "></div>
                            </div>
                            <p class="modal-floor-item-text">Женщина</p>
                        </div>
                    </div>
                    <div class="date-modal">
                        <label class="modal-bold" for="date">Дата рождения</label>

                        <input
                        @change="validateDate" 
                        v-model="registerDate.date" 
                        class="margin-modal" 
                        id="date" 
                        placeholder="ДД.ММ.ГГГГ" 
                        type="text">

                    </div>
                    <div class="modal-date">

                        <p class="modal-bold">Данные для входа в сервис</p>
                        <input 
                        class="margin-modal" 
                        type="email" 
                        placeholder="Email"
                        v-model="registerDate.Email"
                        @change="validateEmail"
                        >

                        <input 
                        class="margin-modal" 
                        type="password" 
                        placeholder="Пароль"
                        v-model="registerDate.Password"
                        @change="validateSize($event,5)"
                        >
                    </div>
                    <div @click="isChecd" class="modal-chec">
                        <div class="modal-chec-inner">
                            <div class="modal-chec__content" :class="checed"></div>
                        </div>
                        <p class="modal-chec-title"> Получать спец предложения</p>
                    </div>
                    <div class="modal-button">
                        <button 
                        :disabled="!btnDisabled"
                        class="button button__modal"
                        @click="sendForm"
                        >Перейти к оплате<span class="modal-arrow">&#10132;</span></button>
                    </div>
                    <div class="modal-footer">
                        <p  class="footer-title">Уже есть аккаунт на Toxin</p>
                        <button @click="openEnt" class="button button__enter">Войти</button>
                    </div>
                </div>
            </div>
</template>

<script >
export default{
    data(){
    return{
        registerDate:{
            firstName:'',
            LastName:'',
            floor: this.male ? 'Мужчина': 'Женщина',
            date:'',
            Email:'',
            Password:'',
            spam:false,
        },
        male: false,
        checed:{
            'modal-chec__spam':false,
        },
    }
    },
    computed:{ 
        valueSize(){
            return (val,size) => val <= size ? 'validete_size-noDone' : 'validete_size-Done' 
        },

        validateRegular(){
            return (val,reg) => reg.test(val) ? 'validete_size-Done' : 'validete_size-noDone' 
        },

        btnDisabled(){
            let reg = /^([a-z0-9_-]+\.)*[a-z0-9_-]+@[a-z0-9_-]+(\.[a-z0-9_-]+)*\.[a-z]{2,6}$/
            return this.registerDate.Password.length > 5 && reg.test(this.registerDate.Email) ?  true : false
        }
    },


    methods:{
        isChecd(){
          this.checed['modal-chec__spam'] = !this.checed['modal-chec__spam']
          this.registerDate.spam = !this.registerDate.spam
        },
        
        sendForm(){
            localStorage.setItem('User',JSON.stringify(this.registerDate))
            this.$emit('CloseRegister',)
        },

        openEnt(){
            this.$emit('openEntries')    
        },

        validateSize({target},size){
            let value = target.value.length 
            target.classList.add(this.valueSize(value,size))
        },

        validateDate({target}){
            let value = target.value 
            let reg = /(0[1-9]|[12][0-9]|3[01])[- /.](0[1-9]|1[012])[- /.](19|20)\d\d/
            target.classList.add(this.validateRegular(value,reg))
        },

        validateEmail({target}){
            let value = target.value 
            let reg = /^([a-z0-9_-]+\.)*[a-z0-9_-]+@[a-z0-9_-]+(\.[a-z0-9_-]+)*\.[a-z]{2,6}$/
            target.classList.add(this.validateRegular(value,reg))
        },
    }
}
</script>


<style>
.validete_size-noDone{
    border-color: #ff0000 !important;
}
.validete_size-Done{
    border-color: #6600ff !important;
}
.modal{
    z-index: 1;
    background-color: #fff;
}
.modal-chec__content{
    transition: all 0.1s ease-in;
}
.modal-chec__spam{
    transform: translateX(17px);
    background-color: #BC9CFF;
}
</style>
